import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { useAuth } from './useAuth';

interface KeywordResearch {
  id: string;
  keyword: string;
  search_volume: number;
  difficulty: number;
  cpc: number;
  related_keywords: string[];
  created_at: string;
}

interface ContentSuggestion {
  id: string;
  url: string;
  target_keyword: string;
  content_score: number;
  suggestions: {
    title?: string;
    meta_description?: string;
    headers?: string[];
    content_gaps?: string[];
    word_count?: {
      current: number;
      recommended: number;
    };
  };
  created_at: string;
  updated_at: string;
}

export function useSeoResearch() {
  const { user } = useAuth();
  const [keywordResearch, setKeywordResearch] = useState<KeywordResearch[]>([]);
  const [contentSuggestions, setContentSuggestions] = useState<ContentSuggestion[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!user) return;
    loadData();
  }, [user]);

  const loadData = async () => {
    try {
      setLoading(true);
      setError(null);

      const [keywordData, suggestionData] = await Promise.all([
        supabase
          .from('seo_keyword_research')
          .select('*')
          .order('created_at', { ascending: false }),
        supabase
          .from('seo_content_suggestions')
          .select('*')
          .order('updated_at', { ascending: false })
      ]);

      if (keywordData.error) throw keywordData.error;
      if (suggestionData.error) throw suggestionData.error;

      setKeywordResearch(keywordData.data || []);
      setContentSuggestions(suggestionData.data || []);
    } catch (err) {
      console.error('Error loading SEO research data:', err);
      setError('Failed to load SEO research data');
    } finally {
      setLoading(false);
    }
  };

  const addKeywordResearch = async (
    keyword: string,
    searchVolume: number,
    difficulty: number,
    cpc: number,
    relatedKeywords: string[]
  ) => {
    try {
      const { data, error } = await supabase
        .from('seo_keyword_research')
        .insert({
          keyword,
          search_volume: searchVolume,
          difficulty,
          cpc,
          related_keywords: relatedKeywords,
          user_id: user?.id
        })
        .select()
        .single();

      if (error) throw error;
      setKeywordResearch(prev => [data, ...prev]);
      return data;
    } catch (err) {
      console.error('Error adding keyword research:', err);
      throw err;
    }
  };

  const addContentSuggestion = async (
    url: string,
    targetKeyword: string,
    contentScore: number,
    suggestions: ContentSuggestion['suggestions']
  ) => {
    try {
      const { data, error } = await supabase
        .from('seo_content_suggestions')
        .insert({
          url,
          target_keyword: targetKeyword,
          content_score: contentScore,
          suggestions,
          user_id: user?.id
        })
        .select()
        .single();

      if (error) throw error;
      setContentSuggestions(prev => [data, ...prev]);
      return data;
    } catch (err) {
      console.error('Error adding content suggestion:', err);
      throw err;
    }
  };

  return {
    keywordResearch,
    contentSuggestions,
    loading,
    error,
    addKeywordResearch,
    addContentSuggestion,
    refresh: loadData
  };
}