import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { useAuth } from './useAuth';
import { showError, showSuccess } from '../lib/toast';

interface SeoTracking {
  id: string;
  user_id: string;
  keyword: string;
  ranking: number | null;
  url: string | null;
  tracked_at: string;
  created_at: string;
}

export function useSeoTracking() {
  const { user } = useAuth();
  const [trackings, setTrackings] = useState<SeoTracking[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!user) return;
    loadTrackings();
  }, [user]);

  const loadTrackings = async () => {
    try {
      setLoading(true);
      setError(null);

      const { data, error: fetchError } = await supabase
        .from('seo_tracking')
        .select('*')
        .eq('user_id', user?.id)
        .order('tracked_at', { ascending: false });

      if (fetchError) throw fetchError;
      setTrackings(data || []);
    } catch (err) {
      console.error('Error loading SEO trackings:', err);
      setError('Failed to load SEO tracking data');
      showError('Failed to load SEO tracking data');
    } finally {
      setLoading(false);
    }
  };

  const addKeyword = async (keyword: string, url?: string) => {
    try {
      const { data, error: insertError } = await supabase
        .from('seo_tracking')
        .insert({
          keyword,
          url,
          user_id: user?.id
        })
        .select()
        .single();

      if (insertError) throw insertError;
      setTrackings(prev => [data, ...prev]);
      showSuccess('Keyword added successfully');
      return data;
    } catch (err) {
      console.error('Error adding keyword:', err);
      showError('Failed to add keyword');
      throw err;
    }
  };

  const updateKeyword = async (id: string, updates: { keyword?: string; url?: string | null }) => {
    try {
      const { data, error: updateError } = await supabase
        .from('seo_tracking')
        .update(updates)
        .eq('id', id)
        .eq('user_id', user?.id)
        .select()
        .single();

      if (updateError) throw updateError;
      setTrackings(prev => prev.map(track => track.id === id ? data : track));
      showSuccess('Keyword updated successfully');
      return data;
    } catch (err) {
      console.error('Error updating keyword:', err);
      showError('Failed to update keyword');
      throw err;
    }
  };

  const updateRanking = async (id: string, ranking: number) => {
    try {
      const { data, error: updateError } = await supabase
        .from('seo_tracking')
        .update({
          ranking,
          tracked_at: new Date().toISOString()
        })
        .eq('id', id)
        .eq('user_id', user?.id)
        .select()
        .single();

      if (updateError) throw updateError;
      
      // Add to historical data
      const { error: historyError } = await supabase
        .from('seo_historical_data')
        .insert({
          keyword_id: id,
          ranking,
          user_id: user?.id
        });

      if (historyError) throw historyError;

      setTrackings(prev => prev.map(track => track.id === id ? data : track));
      showSuccess('Ranking updated successfully');
      return data;
    } catch (err) {
      console.error('Error updating ranking:', err);
      showError('Failed to update ranking');
      throw err;
    }
  };

  const deleteKeyword = async (id: string) => {
    try {
      const { error: deleteError } = await supabase
        .from('seo_tracking')
        .delete()
        .eq('id', id)
        .eq('user_id', user?.id);

      if (deleteError) throw deleteError;
      setTrackings(prev => prev.filter(track => track.id !== id));
      showSuccess('Keyword deleted successfully');
    } catch (err) {
      console.error('Error deleting keyword:', err);
      showError('Failed to delete keyword');
      throw err;
    }
  };

  return {
    trackings,
    loading,
    error,
    addKeyword,
    updateKeyword,
    updateRanking,
    deleteKeyword,
    refresh: loadTrackings
  };
}