import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { useAuth } from './useAuth';

interface SocialAnalytics {
  id: string;
  post_id: string;
  platform: string;
  impressions: number;
  engagement: number;
  clicks: number;
  shares: number;
  likes: number;
  comments: number;
  recorded_at: string;
}

interface AnalyticsSummary {
  totalEngagement: number;
  totalImpressions: number;
  engagementRate: number;
  platformBreakdown: Record<string, {
    posts: number;
    engagement: number;
    impressions: number;
  }>;
  timePerformance: {
    time: string;
    engagement: number;
  }[];
  recentTrends: {
    date: string;
    engagement: number;
    impressions: number;
  }[];
}

export function useSocialAnalytics() {
  const { user } = useAuth();
  const [analytics, setAnalytics] = useState<SocialAnalytics[]>([]);
  const [summary, setSummary] = useState<AnalyticsSummary>({
    totalEngagement: 0,
    totalImpressions: 0,
    engagementRate: 0,
    platformBreakdown: {},
    timePerformance: [],
    recentTrends: []
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!user) return;
    loadAnalytics();
  }, [user]);

  const loadAnalytics = async () => {
    try {
      setLoading(true);
      setError(null);

      const { data, error } = await supabase
        .from('social_analytics')
        .select('*, social_media_posts (platform, created_at)')
        .order('recorded_at', { ascending: false });

      if (error) throw error;

      setAnalytics(data || []);
      calculateSummary(data || []);
    } catch (err) {
      console.error('Error loading social analytics:', err);
      setError('Failed to load analytics data');
    } finally {
      setLoading(false);
    }
  };

  const calculateSummary = (data: SocialAnalytics[]) => {
    const platformBreakdown: Record<string, {
      posts: number;
      engagement: number;
      impressions: number;
    }> = {};

    let totalEngagement = 0;
    let totalImpressions = 0;

    // Calculate platform breakdown and totals
    data.forEach(item => {
      const platform = item.platform;
      if (!platformBreakdown[platform]) {
        platformBreakdown[platform] = {
          posts: 0,
          engagement: 0,
          impressions: 0
        };
      }

      platformBreakdown[platform].posts++;
      platformBreakdown[platform].engagement += item.engagement;
      platformBreakdown[platform].impressions += item.impressions;

      totalEngagement += item.engagement;
      totalImpressions += item.impressions;
    });

    // Calculate time performance
    const timePerformance = calculateTimePerformance(data);

    // Calculate recent trends
    const recentTrends = calculateRecentTrends(data);

    setSummary({
      totalEngagement,
      totalImpressions,
      engagementRate: totalImpressions > 0 ? (totalEngagement / totalImpressions) * 100 : 0,
      platformBreakdown,
      timePerformance,
      recentTrends
    });
  };

  const calculateTimePerformance = (data: SocialAnalytics[]) => {
    const timeSlots: Record<string, { count: number; engagement: number }> = {};
    
    data.forEach(item => {
      const hour = new Date(item.recorded_at).getHours();
      const timeSlot = hour.toString().padStart(2, '0') + ':00';
      
      if (!timeSlots[timeSlot]) {
        timeSlots[timeSlot] = { count: 0, engagement: 0 };
      }
      
      timeSlots[timeSlot].count++;
      timeSlots[timeSlot].engagement += item.engagement;
    });

    return Object.entries(timeSlots)
      .map(([time, stats]) => ({
        time,
        engagement: stats.engagement / stats.count
      }))
      .sort((a, b) => {
        const timeA = parseInt(a.time.split(':')[0]);
        const timeB = parseInt(b.time.split(':')[0]);
        return timeA - timeB;
      });
  };

  const calculateRecentTrends = (data: SocialAnalytics[]) => {
    const dailyStats: Record<string, {
      engagement: number;
      impressions: number;
      count: number;
    }> = {};

    data.forEach(item => {
      const date = new Date(item.recorded_at).toISOString().split('T')[0];
      
      if (!dailyStats[date]) {
        dailyStats[date] = {
          engagement: 0,
          impressions: 0,
          count: 0
        };
      }

      dailyStats[date].engagement += item.engagement;
      dailyStats[date].impressions += item.impressions;
      dailyStats[date].count++;
    });

    return Object.entries(dailyStats)
      .map(([date, stats]) => ({
        date,
        engagement: stats.engagement / stats.count,
        impressions: stats.impressions / stats.count
      }))
      .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime())
      .slice(-7); // Last 7 days
  };

  return {
    analytics,
    summary,
    loading,
    error,
    refresh: loadAnalytics
  };
}