import { useState, useEffect } from 'react';
import { supabase, retryOperation } from '../lib/supabase';
import { useAuth } from './useAuth';
import { showError } from '../lib/toast';

interface UserSettings {
  id: string;
  user_id: string;
  theme: 'light' | 'dark';
  email_notifications: boolean;
  created_at: string;
  updated_at: string;
}

const DEFAULT_SETTINGS: Omit<UserSettings, 'id' | 'user_id' | 'created_at' | 'updated_at'> = {
  theme: 'dark',
  email_notifications: true
};

export function useSettings() {
  const { user } = useAuth();
  const [settings, setSettings] = useState<UserSettings | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!user) return;

    let mounted = true;

    const loadSettings = async () => {
      try {
        setLoading(true);
        setError(null);

        // Try to get existing settings
        const { data: existingSettings, error: fetchError } = await retryOperation(() =>
          supabase
            .from('user_settings')
            .select('*')
            .eq('user_id', user.id)
            .maybeSingle()
        );

        if (fetchError && fetchError.code !== 'PGRST116') {
          throw fetchError;
        }

        // If no settings exist, create default settings
        if (!existingSettings) {
          const { data: newSettings, error: createError } = await retryOperation(() =>
            supabase
              .from('user_settings')
              .insert({
                user_id: user.id,
                ...DEFAULT_SETTINGS
              })
              .select()
              .single()
          );

          if (createError) throw createError;
          if (mounted) setSettings(newSettings);
        } else if (mounted) {
          setSettings(existingSettings);
        }
      } catch (err) {
        console.error('Error loading settings:', err);
        if (mounted) {
          setError('Failed to load settings');
          // Use default settings as fallback
          setSettings({
            id: 'temp',
            user_id: user.id,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            ...DEFAULT_SETTINGS
          });
        }
      } finally {
        if (mounted) {
          setLoading(false);
        }
      }
    };

    loadSettings();

    return () => {
      mounted = false;
    };
  }, [user]);

  const updateSettings = async (updates: Partial<Omit<UserSettings, 'id' | 'user_id' | 'created_at' | 'updated_at'>>) => {
    if (!user) return;

    try {
      const { data, error } = await retryOperation(() =>
        supabase
          .from('user_settings')
          .upsert({
            user_id: user.id,
            ...DEFAULT_SETTINGS,
            ...updates
          })
          .select()
          .single()
      );

      if (error) throw error;
      setSettings(data);
      return data;
    } catch (err) {
      console.error('Error updating settings:', err);
      showError('Failed to update settings');
      throw err;
    }
  };

  return {
    settings,
    loading,
    error,
    updateSettings
  };
}