import { useState } from 'react';
import { useAI } from '../../hooks/useAI';
import { useSeoResearch } from '../../hooks/useSeoResearch';
import { Search, TrendingUp, Target, Brain, ArrowUp, ArrowDown, DollarSign } from 'lucide-react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  LineChart,
  Line
} from 'recharts';

interface KeywordResearchProps {
  onKeywordSelect: (keyword: string) => void;
}

export function KeywordResearch({ onKeywordSelect }: KeywordResearchProps) {
  const { keywordResearch, contentSuggestions, loading, addKeywordResearch } = useSeoResearch();
  const [searchTerm, setSearchTerm] = useState('');
  const [analyzing, setAnalyzing] = useState(false);
  const [selectedKeyword, setSelectedKeyword] = useState<string | null>(null);

  const handleAnalyze = async () => {
    if (!searchTerm) return;
    
    setAnalyzing(true);
    try {
      await addKeywordResearch(
        searchTerm,
        Math.floor(Math.random() * 10000), // Simulated search volume
        Math.random() * 100, // Simulated difficulty
        Math.random() * 10, // Simulated CPC
        generateRelatedKeywords(searchTerm)
      );
    } catch (err) {
      console.error('Analysis error:', err);
    } finally {
      setAnalyzing(false);
    }
  };

  const generateRelatedKeywords = (keyword: string) => {
    // Simulated related keywords
    const prefixes = ['best', 'top', 'how to', 'why', 'when'];
    const suffixes = ['guide', 'tutorial', 'tips', 'examples', 'services'];
    
    return [
      ...prefixes.map(prefix => `${prefix} ${keyword}`),
      ...suffixes.map(suffix => `${keyword} ${suffix}`)
    ];
  };

  return (
    <div className="space-y-6">
      {/* Search Bar */}
      <div className="bg-primary-700/50 rounded-xl p-6">
        <div className="flex items-center gap-4 mb-6">
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 text-primary-400 w-5 h-5" />
              <input
                type="text"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                placeholder="Enter a keyword to analyze..."
                className="w-full pl-12 pr-4 py-3 bg-primary-600 border-primary-500 rounded-lg text-white placeholder-primary-400"
              />
            </div>
          </div>
          <button
            onClick={handleAnalyze}
            disabled={analyzing || !searchTerm}
            className="btn-primary py-3 px-6 flex items-center gap-2"
          >
            <Brain className="w-4 h-4" />
            {analyzing ? 'Analyzing...' : 'Analyze'}
          </button>
        </div>

        {/* Keyword Metrics */}
        {keywordResearch.length > 0 && (
          <div className="grid grid-cols-4 gap-6">
            <div className="bg-primary-600/50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-primary-200">Search Volume</span>
                <TrendingUp className="w-4 h-4 text-accent-500" />
              </div>
              <p className="text-2xl font-bold text-white">
                {keywordResearch[0].search_volume.toLocaleString()}
              </p>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-primary-200">Difficulty</span>
                <Target className="w-4 h-4 text-accent-500" />
              </div>
              <p className="text-2xl font-bold text-white">
                {keywordResearch[0].difficulty.toFixed(1)}/100
              </p>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-primary-200">CPC</span>
                <DollarSign className="w-4 h-4 text-accent-500" />
              </div>
              <p className="text-2xl font-bold text-white">
                ${keywordResearch[0].cpc.toFixed(2)}
              </p>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-primary-200">Related Keywords</span>
                <Search className="w-4 h-4 text-accent-500" />
              </div>
              <p className="text-2xl font-bold text-white">
                {keywordResearch[0].related_keywords.length}
              </p>
            </div>
          </div>
        )}
      </div>

      {/* Keyword Analysis */}
      {keywordResearch.length > 0 && (
        <div className="grid grid-cols-2 gap-6">
          {/* Related Keywords */}
          <div className="bg-primary-700/50 rounded-xl p-6">
            <h3 className="text-lg font-medium text-white mb-4">Related Keywords</h3>
            <div className="space-y-3">
              {keywordResearch[0].related_keywords.map((keyword, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between bg-primary-600/50 rounded-lg p-3 cursor-pointer hover:bg-primary-600 transition-colors"
                  onClick={() => {
                    setSelectedKeyword(keyword);
                    onKeywordSelect(keyword);
                  }}
                >
                  <span className="text-white">{keyword}</span>
                  <div className="flex items-center gap-4 text-sm">
                    <span className="text-primary-300">
                      Volume: {Math.floor(Math.random() * 1000).toLocaleString()}
                    </span>
                    <span className="text-primary-300">
                      KD: {Math.floor(Math.random() * 100)}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Trend Analysis */}
          <div className="bg-primary-700/50 rounded-xl p-6">
            <h3 className="text-lg font-medium text-white mb-4">Search Trend</h3>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={[
                  { month: 'Jan', volume: 1200 },
                  { month: 'Feb', volume: 1400 },
                  { month: 'Mar', volume: 1100 },
                  { month: 'Apr', volume: 1600 },
                  { month: 'May', volume: 1800 },
                  { month: 'Jun', volume: 2000 }
                ]}>
                  <XAxis dataKey="month" stroke="#94a3b8" />
                  <YAxis stroke="#94a3b8" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1e293b',
                      border: 'none',
                      borderRadius: '0.5rem',
                      color: '#fff'
                    }}
                  />
                  <Line
                    type="monotone"
                    dataKey="volume"
                    stroke="#f97316"
                    strokeWidth={2}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}