import { supabase } from './supabase';

// Social media platform configurations
const SOCIAL_CONFIGS = {
  twitter: {
    clientId: import.meta.env.VITE_TWITTER_CLIENT_ID,
    redirectUri: import.meta.env.VITE_TWITTER_CALLBACK_URL,
    scope: 'tweet.read tweet.write users.read follows.read',
  },
  facebook: {
    clientId: import.meta.env.VITE_FACEBOOK_CLIENT_ID,
    redirectUri: import.meta.env.VITE_FACEBOOK_CALLBACK_URL,
    scope: 'pages_show_list,pages_read_engagement,pages_manage_posts',
  },
  instagram: {
    clientId: import.meta.env.VITE_INSTAGRAM_CLIENT_ID,
    redirectUri: import.meta.env.VITE_INSTAGRAM_CALLBACK_URL,
    scope: 'instagram_basic,instagram_content_publish',
  },
  linkedin: {
    clientId: import.meta.env.VITE_LINKEDIN_CLIENT_ID,
    redirectUri: import.meta.env.VITE_LINKEDIN_CALLBACK_URL,
    scope: 'r_liteprofile w_member_social',
  }
};

export async function connectSocialAccount(platform: keyof typeof SOCIAL_CONFIGS) {
  const config = SOCIAL_CONFIGS[platform];
  
  // Validate configuration
  if (!config) {
    throw new Error(`Unsupported platform: ${platform}`);
  }

  // Check for missing configuration
  if (!config.clientId) {
    throw new Error('Social media integration is not configured. Please set up your OAuth credentials.');
  }

  try {
    // Store the platform in session for the callback
    sessionStorage.setItem('connecting_platform', platform);

    // Generate and store state for security
    const state = Math.random().toString(36).substring(7);
    sessionStorage.setItem('oauth_state', state);

    // Build the OAuth URL
    const params = new URLSearchParams({
      client_id: config.clientId,
      redirect_uri: config.redirectUri,
      scope: config.scope,
      response_type: 'code',
      state,
    });

    // Redirect to OAuth provider
    window.location.href = `https://${platform}.com/oauth/authorize?${params.toString()}`;
  } catch (error) {
    console.error('Error initiating social connection:', error);
    throw new Error('Failed to connect account. Please check your configuration and try again.');
  }
}

export async function handleOAuthCallback(platform: string, code: string, state: string) {
  // Verify state to prevent CSRF
  const storedState = sessionStorage.getItem('oauth_state');
  if (!storedState || state !== storedState) {
    throw new Error('Invalid OAuth state. Please try connecting again.');
  }

  try {
    // Check if account already exists
    const { data: existingAccount } = await supabase
      .from('social_media_accounts')
      .select('id')
      .eq('platform', platform)
      .maybeSingle();

    if (existingAccount) {
      throw new Error(`A ${platform} account is already connected.`);
    }

    // Store the new connection
    const { data, error } = await supabase
      .from('social_media_accounts')
      .insert({
        platform,
        account_name: `${platform}_user`,
        status: 'active',
        metadata: {
          connected_at: new Date().toISOString(),
          last_verified: new Date().toISOString()
        }
      })
      .select()
      .single();

    if (error) throw error;

    // Clean up session storage
    sessionStorage.removeItem('connecting_platform');
    sessionStorage.removeItem('oauth_state');

    return data;
  } catch (error) {
    console.error('OAuth callback error:', error);
    throw error instanceof Error ? error : new Error('Failed to complete social media connection');
  }
}