import { useState } from 'react';
import { useAI } from '../../hooks/useAI';
import {
  Settings,
  Smartphone,
  Zap,
  Search,
  AlertCircle,
  CheckCircle,
  ExternalLink,
  Download,
  Code,
  RefreshCw
} from 'lucide-react';

interface TechnicalSeoProps {
  onIssueFixed: (issue: any) => void;
}

export function TechnicalSeo({ onIssueFixed }: TechnicalSeoProps) {
  const [scanning, setScanning] = useState(false);
  const [selectedTab, setSelectedTab] = useState<'health' | 'mobile' | 'speed' | 'crawl'>('health');
  const [issues, setIssues] = useState<any[]>([]);

  const handleScan = async () => {
    setScanning(true);
    try {
      // Simulated technical SEO scan results
      const results = [
        {
          type: 'critical',
          category: 'health',
          issue: 'Missing SSL Certificate',
          impact: 'Security and SEO',
          recommendation: 'Install SSL certificate to enable HTTPS'
        },
        {
          type: 'warning',
          category: 'mobile',
          issue: 'Non-responsive elements detected',
          impact: 'Mobile UX',
          recommendation: 'Update CSS for mobile responsiveness'
        },
        {
          type: 'critical',
          category: 'speed',
          issue: 'Large unoptimized images',
          impact: 'Page Load Speed',
          recommendation: 'Compress and optimize images'
        }
      ];
      
      setIssues(results);
    } catch (err) {
      console.error('Scan error:', err);
    } finally {
      setScanning(false);
    }
  };

  const renderTabContent = () => {
    switch (selectedTab) {
      case 'health':
        return (
          <div className="space-y-4">
            <div className="grid grid-cols-3 gap-6">
              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">SSL Status</span>
                  <CheckCircle className="w-4 h-4 text-green-400" />
                </div>
                <p className="text-white font-medium">Secure (HTTPS)</p>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">Robots.txt</span>
                  <AlertCircle className="w-4 h-4 text-yellow-400" />
                </div>
                <p className="text-white font-medium">Needs Update</p>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">Sitemap</span>
                  <CheckCircle className="w-4 h-4 text-green-400" />
                </div>
                <p className="text-white font-medium">Valid XML</p>
              </div>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <h4 className="text-lg font-medium text-white mb-4">Health Check Results</h4>
              <div className="space-y-3">
                {issues
                  .filter(issue => issue.category === 'health')
                  .map((issue, index) => (
                    <div
                      key={index}
                      className={`p-4 rounded-lg ${
                        issue.type === 'critical'
                          ? 'bg-red-500/10 border-l-4 border-red-500'
                          : 'bg-yellow-500/10 border-l-4 border-yellow-500'
                      }`}
                    >
                      <div className="flex items-start gap-3">
                        {issue.type === 'critical' ? (
                          <AlertCircle className="w-5 h-5 text-red-500 mt-0.5" />
                        ) : (
                          <AlertCircle className="w-5 h-5 text-yellow-500 mt-0.5" />
                        )}
                        <div>
                          <p className="text-white font-medium">{issue.issue}</p>
                          <p className="text-primary-300 text-sm mt-1">
                            {issue.recommendation}
                          </p>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        );

      case 'mobile':
        return (
          <div className="space-y-4">
            <div className="grid grid-cols-2 gap-6">
              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-lg font-medium text-white">Mobile Score</h4>
                  <Smartphone className="w-5 h-5 text-accent-500" />
                </div>
                <div className="flex items-center gap-4">
                  <div className="text-3xl font-bold text-white">86</div>
                  <div className="text-sm text-primary-300">/ 100</div>
                </div>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-lg font-medium text-white">Viewport</h4>
                  <CheckCircle className="w-5 h-5 text-green-400" />
                </div>
                <p className="text-primary-300">Properly configured for mobile devices</p>
              </div>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <h4 className="text-lg font-medium text-white mb-4">Mobile Issues</h4>
              <div className="space-y-3">
                {issues
                  .filter(issue => issue.category === 'mobile')
                  .map((issue, index) => (
                    <div
                      key={index}
                      className="p-4 bg-primary-500/20 rounded-lg"
                    >
                      <div className="flex items-start gap-3">
                        <AlertCircle className="w-5 h-5 text-yellow-500 mt-0.5" />
                        <div>
                          <p className="text-white font-medium">{issue.issue}</p>
                          <p className="text-primary-300 text-sm mt-1">
                            {issue.recommendation}
                          </p>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        );

      case 'speed':
        return (
          <div className="space-y-4">
            <div className="grid grid-cols-3 gap-6">
              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">Load Time</span>
                  <Zap className="w-4 h-4 text-accent-500" />
                </div>
                <p className="text-2xl font-bold text-white">2.3s</p>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">First Paint</span>
                  <Zap className="w-4 h-4 text-accent-500" />
                </div>
                <p className="text-2xl font-bold text-white">0.8s</p>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-primary-200">TTFB</span>
                  <Zap className="w-4 h-4 text-accent-500" />
                </div>
                <p className="text-2xl font-bold text-white">180ms</p>
              </div>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <h4 className="text-lg font-medium text-white mb-4">Speed Optimization</h4>
              <div className="space-y-3">
                {issues
                  .filter(issue => issue.category === 'speed')
                  .map((issue, index) => (
                    <div
                      key={index}
                      className="p-4 bg-primary-500/20 rounded-lg"
                    >
                      <div className="flex items-start gap-3">
                        <AlertCircle className="w-5 h-5 text-red-500 mt-0.5" />
                        <div>
                          <p className="text-white font-medium">{issue.issue}</p>
                          <p className="text-primary-300 text-sm mt-1">
                            {issue.recommendation}
                          </p>
                          <button
                            onClick={() => onIssueFixed(issue)}
                            className="mt-2 text-sm text-accent-500 hover:text-accent-400"
                          >
                            Fix Issue
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        );

      case 'crawl':
        return (
          <div className="space-y-4">
            <div className="grid grid-cols-2 gap-6">
              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-lg font-medium text-white">Crawl Status</h4>
                  <Search className="w-5 h-5 text-accent-500" />
                </div>
                <div className="flex items-center gap-2">
                  <CheckCircle className="w-5 h-5 text-green-400" />
                  <span className="text-white">Crawlable</span>
                </div>
              </div>

              <div className="bg-primary-600/50 rounded-lg p-4">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-lg font-medium text-white">Indexed Pages</h4>
                  <Code className="w-5 h-5 text-accent-500" />
                </div>
                <p className="text-2xl font-bold text-white">128</p>
              </div>
            </div>

            <div className="bg-primary-600/50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-4">
                <h4 className="text-lg font-medium text-white">Crawl Configuration</h4>
                <div className="flex gap-2">
                  <button className="btn-secondary py-2 px-4 flex items-center gap-2">
                    <Download className="w-4 h-4" />
                    Download Sitemap
                  </button>
                  <button className="btn-secondary py-2 px-4 flex items-center gap-2">
                    <Code className="w-4 h-4" />
                    View robots.txt
                  </button>
                </div>
              </div>
            </div>
          </div>
        );
    }
  };

  return (
    <div className="space-y-6">
      <div className="bg-primary-700/50 rounded-xl p-6">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <Settings className="w-5 h-5 text-accent-500" />
            <h3 className="text-lg font-medium text-white">Technical SEO</h3>
          </div>
          <button
            onClick={handleScan}
            disabled={scanning}
            className="btn-primary py-2 px-4 flex items-center gap-2"
          >
            <RefreshCw className={`w-4 h-4 ${scanning ? 'animate-spin' : ''}`} />
            {scanning ? 'Scanning...' : 'Run Scan'}
          </button>
        </div>

        {/* Navigation Tabs */}
        <div className="flex space-x-4 border-b border-primary-600 mb-6">
          <button
            onClick={() => setSelectedTab('health')}
            className={`px-4 py-2 ${
              selectedTab === 'health'
                ? 'text-accent-500 border-b-2 border-accent-500'
                : 'text-primary-300 hover:text-white'
            }`}
          >
            Site Health
          </button>
          <button
            onClick={() => setSelectedTab('mobile')}
            className={`px-4 py-2 ${
              selectedTab === 'mobile'
                ? 'text-accent-500 border-b-2 border-accent-500'
                : 'text-primary-300 hover:text-white'
            }`}
          >
            Mobile Usability
          </button>
          <button
            onClick={() => setSelectedTab('speed')}
            className={`px-4 py-2 ${
              selectedTab === 'speed'
                ? 'text-accent-500 border-b-2 border-accent-500'
                : 'text-primary-300 hover:text-white'
            }`}
          >
            Page Speed
          </button>
          <button
            onClick={() => setSelectedTab('crawl')}
            className={`px-4 py-2 ${
              selectedTab === 'crawl'
                ? 'text-accent-500 border-b-2 border-accent-500'
                : 'text-primary-300 hover:text-white'
            }`}
          >
            Crawlability
          </button>
        </div>

        {/* Tab Content */}
        {renderTabContent()}
      </div>
    </div>
  );
}