import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../lib/supabase';
import { useAuth } from '../hooks/useAuth';

export function AuthCallback() {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function handleCallback() {
      try {
        // Get the platform and state from session storage
        const platform = sessionStorage.getItem('connecting_platform');
        const storedState = sessionStorage.getItem('oauth_state');
        
        if (!platform || !storedState) {
          throw new Error('Invalid callback state');
        }

        // Get the code and state from URL params
        const params = new URLSearchParams(window.location.search);
        const code = params.get('code');
        const state = params.get('state');

        if (!code || !state) {
          throw new Error('Missing required parameters');
        }

        // Verify state to prevent CSRF
        if (state !== storedState) {
          throw new Error('Invalid state parameter');
        }

        // Store the social media account in Supabase
        const { error: dbError } = await supabase
          .from('social_media_accounts')
          .insert({
            user_id: user?.id,
            platform,
            account_name: `${platform}_user`, // This would come from the platform's API
            access_token: code, // In production, exchange code for token
            status: 'active'
          });

        if (dbError) throw dbError;

        // Clean up session storage
        sessionStorage.removeItem('connecting_platform');
        sessionStorage.removeItem('oauth_state');

        // Redirect back to the social media dashboard
        navigate('/social');
      } catch (err) {
        console.error('Error handling callback:', err);
        setError(err instanceof Error ? err.message : 'Failed to connect account');
      }
    }

    handleCallback();
  }, [navigate, user]);

  if (error) {
    return (
      <div className="min-h-screen bg-primary-800 flex items-center justify-center p-4">
        <div className="bg-red-500/10 border border-red-500/50 text-red-500 rounded-lg p-4 max-w-md">
          <h2 className="text-lg font-medium mb-2">Connection Failed</h2>
          <p>{error}</p>
          <button
            onClick={() => navigate('/social')}
            className="mt-4 text-white bg-red-500 px-4 py-2 rounded-lg hover:bg-red-600 transition-colors"
          >
            Return to Dashboard
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-primary-800 flex items-center justify-center">
      <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-accent-500"></div>
    </div>
  );
}