import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { useAuth } from './useAuth';
import { showError, showSuccess } from '../lib/toast';

interface SocialMediaAccount {
  id: string;
  user_id: string;
  platform: string;
  account_name: string;
  status: string;
  metadata: Record<string, any>;
  created_at: string;
}

interface SocialMediaPost {
  id: string;
  user_id: string;
  account_id: string;
  content: string;
  media_urls: string[];
  platform: string;
  scheduled_for?: string;
  published_at?: string;
  status: string;
  metadata: Record<string, any>;
  created_at: string;
}

export function useSocialMedia() {
  const { user } = useAuth();
  const [accounts, setAccounts] = useState<SocialMediaAccount[]>([]);
  const [posts, setPosts] = useState<SocialMediaPost[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!user) return;

    let mounted = true;

    const loadData = async () => {
      try {
        setLoading(true);
        setError(null);

        const [accountsData, postsData] = await Promise.all([
          supabase
            .from('social_media_accounts')
            .select('*')
            .eq('user_id', user.id)
            .order('created_at', { ascending: false }),
          supabase
            .from('social_media_posts')
            .select('*')
            .eq('user_id', user.id)
            .order('created_at', { ascending: false })
        ]);

        if (accountsData.error) throw accountsData.error;
        if (postsData.error) throw postsData.error;

        if (mounted) {
          setAccounts(accountsData.data || []);
          setPosts(postsData.data || []);
        }
      } catch (err) {
        console.error('Error loading social media data:', err);
        if (mounted) {
          setError('Failed to load social media data');
          showError('Failed to load social media data');
        }
      } finally {
        if (mounted) {
          setLoading(false);
        }
      }
    };

    // Initial load
    loadData();

    // Subscribe to real-time updates
    const accountsSubscription = supabase
      .channel('social_accounts_changes')
      .on('postgres_changes', {
        event: '*',
        schema: 'public',
        table: 'social_media_accounts',
        filter: `user_id=eq.${user.id}`
      }, () => {
        if (mounted) loadData();
      })
      .subscribe();

    const postsSubscription = supabase
      .channel('social_posts_changes')
      .on('postgres_changes', {
        event: '*',
        schema: 'public',
        table: 'social_media_posts',
        filter: `user_id=eq.${user.id}`
      }, () => {
        if (mounted) loadData();
      })
      .subscribe();

    return () => {
      mounted = false;
      accountsSubscription.unsubscribe();
      postsSubscription.unsubscribe();
    };
  }, [user]);

  const addAccount = async (platform: string, accountName: string) => {
    try {
      const { data, error: insertError } = await supabase
        .from('social_media_accounts')
        .insert({
          platform,
          account_name: accountName,
          user_id: user?.id,
          status: 'active'
        })
        .select()
        .single();

      if (insertError) throw insertError;
      setAccounts(prev => [data, ...prev]);
      showSuccess('Account added successfully');
      return data;
    } catch (err) {
      console.error('Error adding social media account:', err);
      showError('Failed to add account');
      throw err;
    }
  };

  const createPost = async (
    accountId: string,
    content: string,
    mediaUrls: string[] = [],
    scheduledFor?: Date
  ) => {
    try {
      const account = accounts.find(a => a.id === accountId);
      if (!account) throw new Error('Account not found');

      const { data, error: insertError } = await supabase
        .from('social_media_posts')
        .insert({
          account_id: accountId,
          content,
          media_urls: mediaUrls,
          platform: account.platform,
          scheduled_for: scheduledFor?.toISOString(),
          status: scheduledFor ? 'scheduled' : 'draft',
          user_id: user?.id
        })
        .select()
        .single();

      if (insertError) throw insertError;
      setPosts(prev => [data, ...prev]);
      showSuccess('Post created successfully');
      return data;
    } catch (err) {
      console.error('Error creating post:', err);
      showError('Failed to create post');
      throw err;
    }
  };

  const updatePostStatus = async (postId: string, status: string, publishedAt?: Date) => {
    try {
      const { data, error: updateError } = await supabase
        .from('social_media_posts')
        .update({
          status,
          published_at: publishedAt?.toISOString()
        })
        .eq('id', postId)
        .eq('user_id', user?.id)
        .select()
        .single();

      if (updateError) throw updateError;
      setPosts(prev => prev.map(post => post.id === postId ? data : post));
      showSuccess('Post status updated successfully');
      return data;
    } catch (err) {
      console.error('Error updating post status:', err);
      showError('Failed to update post status');
      throw err;
    }
  };

  return {
    accounts,
    posts,
    loading,
    error,
    addAccount,
    createPost,
    updatePostStatus
  };
}