import { createClient } from '@supabase/supabase-js';
import type { Database } from '../types/database';
import { showError, showSuccess } from './toast';

const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

if (!supabaseUrl || !supabaseAnonKey) {
  console.warn('Missing Supabase environment variables');
}

export const supabase = createClient<Database>(
  supabaseUrl || '',
  supabaseAnonKey || '',
  {
    auth: {
      autoRefreshToken: true,
      persistSession: true,
      detectSessionInUrl: true,
      storage: typeof window !== 'undefined' ? window.localStorage : undefined
    },
    global: {
      headers: {
        'X-Client-Info': 'getgetleads-web'
      }
    }
  }
);

// Retry configuration
const MAX_RETRIES = 3;
const INITIAL_BACKOFF = 1000; // 1 second

// Helper to determine if an error is retryable
function isRetryableError(error: any): boolean {
  const retryableErrors = [
    'AuthRetryableFetchError',
    'FetchError',
    'TimeoutError',
    'NetworkError',
    'AbortError'
  ];

  return (
    error instanceof Error && (
      retryableErrors.some(code => error.message.includes(code)) ||
      error.message.includes('Failed to fetch') ||
      error.message.includes('network') ||
      error.message.includes('timeout') ||
      error.message.includes('connection')
    )
  );
}

// Enhanced retry helper with exponential backoff
export async function retryOperation<T>(
  operation: () => Promise<T>,
  retries = MAX_RETRIES,
  backoff = INITIAL_BACKOFF
): Promise<T> {
  try {
    return await operation();
  } catch (error) {
    if (retries > 0 && isRetryableError(error)) {
      await new Promise(resolve => setTimeout(resolve, backoff));
      return retryOperation(operation, retries - 1, backoff * 2);
    }
    throw error;
  }
}

// Auth state change handler
supabase.auth.onAuthStateChange((event, session) => {
  if (event === 'SIGNED_OUT') {
    localStorage.removeItem('supabase.auth.token');
    showSuccess('Successfully signed out');
  } else if (event === 'SIGNED_IN') {
    showSuccess('Successfully signed in');
  } else if (event === 'TOKEN_REFRESHED') {
    console.log('Auth token refreshed');
  } else if (event === 'USER_UPDATED') {
    showSuccess('Profile updated successfully');
  }
});

// Network status monitoring
let isOnline = true;
window.addEventListener('online', () => {
  isOnline = true;
  showSuccess('Back online');
});

window.addEventListener('offline', () => {
  isOnline = false;
  showError('You are offline. Please check your internet connection.');
});

export function checkConnection() {
  return isOnline;
}